import React, { Component } from 'react';

class SliderBar extends Component {

  render() {
    // console.log(this.props.duration)
    // console.log(this.props.currTime);
    const value = this.props.value;
    const width = this.props.isDragging ? value * 100 : (this.props.currTime / this.props.duration) * 100;
    // const width = (this.props.currTime / this.props.duration) * 100;
    // console.log(width);
    const style = {
      width: `${width}%`
    }
    return (
      <div style={style} className="slider-bar-current" />
    );
  }

}

export default SliderBar;
